import React from 'react';
import { Link } from 'react-router-dom';
import Logo from '../assets/img/ravenlogo.png';

export default function Header() {
  return (
    <header className="header">
      <div className="logo">
        <img src={Logo} alt="" />
      </div>

      <nav className="nav">
        <div className="left">
          <Link to={'/'}>Home</Link>
          <Link to={'/about'}>FAQs</Link>
          <Link to={'/contact'}>Contact</Link>
        </div>
        <div className="right">
          <button>Login</button>
        </div>
      </nav>
    </header>
  );
}
