import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Logo from '../assets/img/ravenlogo.png';

export default function Footer() {
  return (
    <footer className="footer">
      <div className="tab-1">
        <div className="logo">
          <img src={Logo} alt="" />
        </div>
        <p>
          Raven is a live-streaming and social networking app helping African
          creators earn globally
        </p>
      </div>

      <div className="tab-2">
        <div className="social-media">
          <FontAwesomeIcon icon={['fab', 'facebook']} size="2x" />
          <FontAwesomeIcon icon={['fab', 'twitter']} size="2x" />
          <FontAwesomeIcon icon={['fab', 'instagram']} size="2x" />
          <FontAwesomeIcon icon={['fab', 'reddit']} size="2x" />
          <FontAwesomeIcon icon={['fab', 'youtube']} size="2x" />
        </div>
        <div className="line-divider"></div>

        <div className="user-permission">
          <a href="#">Safety Guidelines</a>
          <a href="#">Terms of Service</a>
          <a href="#">Privacy Policy</a>
        </div>

        <div className="hide-mobile">
          &copy; 2022 Raven Livestream Technologies Ltd. All rights reserved
        </div>
      </div>

      <div className="tab-3">
        <p>Get the App on</p>

        <div className="download-platforms">
          <button>Apple</button>
          <button>Playstore</button>
        </div>

        <p>Send a mail</p>

        <a href="" className="button">
          Contact@ravenlivestream.com
        </a>
      </div>
    </footer>
  );
}
